.preloader {
    position: fixed;
    top: 0;
    left: 0;
    z-index: 10;
    width: 100%;
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: $first-screen-color;
    transition: .3s;

    svg {
        width: 80px;
        animation: spinning .9s linear infinite;

        path {
            fill: $preloader-color;
        }
    }

    &.hidden_opacity {
        transition: .3s;
        opacity: 0;
    }

    &.hidden {
        display: none;
    }
}

@keyframes spinning {
    from {
        transform: rotate(0);
    }
    to {
        transform: rotate(-360deg);
    }
}